body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quez {
  width: 500px;
  border-radius: 30px;
  margin-bottom: 60px;
  padding: 40px;
  background-color: #fff;
  position: relative;
}
.result {
  text-align: center;
}
.againBtn {
  font-weight: bold;
  border-radius: 30px;
  border: 0;
  padding: 15px 30px;
  font-size: 20px;
  background-color: #ff006e;
  color: #fff;
  margin-top: 20px;
  cursor: pointer;
}
.result h2 {
  margin-bottom: 0;
}
.result img {
  width: 150px;
}
.progress {
  height: 10px;
  border-radius: 30px;
  background-color: rgb(232, 232, 232);
  margin-bottom: 25px;
}
.progressInner {
  height: 100%;
  border-radius: 30px;
  width: 80%;
  background: rgb(18, 231, 48);
  transition: all 0.3s ease-in-out;
  background: linear-gradient(
    90deg,
    rgba(333, 231, 48, 1) 0%,
    rgba(444, 21, 255, 1) 100%
  );
}
.progress:before,
.progress:after {
  content: '';
  display: block;
  height: 50px;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.progress:before {
  width: 95%;
  bottom: -10px;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 0;
}
.progress:after {
  width: 90%;
  bottom: -20px;
  background-color: rgba(255, 255, 255, 0.3);
  z-index: 1;
}
.questionTitle {
  margin: 0;
  font-size: 42px;
}
ul {
  list-style: none;
  padding: 0;
}
ul > li {
  padding: 15px;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}
ul > li:hover {
  border: 2px solid rgba(0, 0, 0, 0.3);
}
